body {
  margin: 0;
  padding: 0;
  position: relative;
}

a {
  text-decoration: none;
}

.search-form {
  background-color: #3b3bb4;
  flex-flow: wrap;
  justify-content: center;
  padding: 20px;
  display: flex;
}

input {
  width: 40%;
  border-color: currentColor;
  border-radius: 10px 0 0 10px;
  padding: 7.5px;
}

.btnSearch {
  box-shadow: none;
  background-color: #fff;
  border-color: currentColor;
  border-radius: 0 10px 10px 0;
  margin: 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 20px auto;
  display: flex;
}

.photo-card {
  width: 310px;
  display: block;
  box-shadow: 10px 10px 22px rgba(0, 0, 0, .75);
}

.info {
  color: #000;
  gap: 14px;
  padding: 6px;
  display: flex;
}

.loading {
  width: 115px;
  color: #fff;
  background-color: #3b3bc3;
  border: none;
  border-radius: 10px;
  margin: 0 auto 20px;
  padding: 10px;
}

